body {
  margin: 0;
}

button {
  cursor: pointer;
  border: none;
  background: none;
  padding: 0;
}

a {
  text-decoration: none;
  outline: none;
}

h1 {
  margin: 0;
  // font-family: Poppins-Bold;
  // font-size: 48px;
  // line-height: 1.5;
  // letter-spacing: 0;
  // @media screen and (min-width: 768px) {
  //   font-size: 96px;
  // }
}

h2 {
  margin: 0;
  font-family: DM-Sans;
  font-size: 32px;
  font-style: normal;
  font-weight: 500;
  line-height: 120%;
  @media screen and (min-width: 768px) {
    font-size: 40px;
  }
  @media screen and (min-width: 1440px) {
    font-size: 64px;
  }
}
h3 {
  margin: 0;
}
h4 {
  margin: 0;
}

h5 {
  margin: 0;
}

p {
  margin-bottom: 0;
  margin-top: 0;
}

ul {
  padding: 0;
  margin-top: 0;
  margin: 0;
  margin-bottom: 0;
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

