.container {
  box-sizing: border-box;
  padding: 32px 24px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 768px) {
    padding: 64px 20px;
  }

  @media screen and (min-width: 1440px) {
    max-width: 1441px;
    padding: 50px 100px;
    // padding-right: 100px;
  }
}
